.wall{
    background: url('https://cdn.pixabay.com/photo/2016/03/10/01/28/orange-1247696_1280.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 300px;
    
}

.wall-img{
    width: 250px;
    height: 250px;
    border-radius: 50%;
    border: 3px solid indigo;
    margin-top: 20px;
    margin-left: 10%;
    background-color: rgb(196, 243, 247);
    transition: transform 2s;
}

.wall-img:hover{
    width: 250px;
    height: 250px;
    border-radius: 50%;
    border: 2px solid white;
    margin-top: 20px;
    margin-left: 10%;
    background-color: rgb(65, 133, 211);
    transform: rotate(360deg);
}

.wall-span{
    font-size: 50px;
    margin-left: 2%;
    font-family: cursive;
    color: rgb(59, 3, 97);
}

.wall-brand{
    width: 100%;
    
}

.wall-brand-img{
    width: 90%;
    border:3px solid tomato;
}

.wall-brand-span{
    font-size: 40px;
    font-family: cursive;
    color: rgb(59, 3, 97);
}

.wall-brand-p{
    font-size: 28px;
}

.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 40%;
    height:350px;
  }


  .card-filter{
      box-shadow:1px 2px 3px 2px rgba(182, 122, 81, 0.5);
      margin-top: 30px;
    background-color: white;
  }

  .span-price{
      display: block;
      color: green;
      text-align: center;
  }

  
  .span-ratings{
    display: block;
    color: rgb(182, 21, 83);
    text-align: center;
}

.span-name{
    font-size: 20px;
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
    height: 50px;
    color: rgb(80, 11, 97);
}


.abc{
    height: 60px;
}

.check-box{
    font-size: 18px;
    color: rgb(77, 73, 73);
    font-family:Georgia, 'Times New Roman', Times, serif;
    margin-left: 10px;
}