.bg-image{
    background: rgb(255, 221, 31);   
    
    background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 100% 100%;

    padding: 50px;
    height: 520px;
    
}

.bg-logo{
    display: block;
    
    
    background-repeat: no-repeat;
    width: 300px;
    height: 300px;
    
    border-radius: 50%;
    color: #fff5ee;
    
    
    margin-left: auto;
    margin-right: auto;
    font-weight: 600;
    background: url('https://www.graphicsprings.com/filestorage/stencils/1834b5e43f0692166cc7ec858cdb5b00.png?width=500&height=500');
    background-position: center;

    background-color: rgb(255, 255, 255);
    border: 5px solid white;
    
}

.bg-text{
    color: crimson;
    font-style: oblique;
    font-size: 60px;
    text-align: center;
    margin-top: 50px;
    margin-left: -20px;
    text-shadow:2px 3px rgba(252, 252, 252, 0.794);
}
.form1{
    margin-left: 40px;
    margin-right: 40px;
    font-size: 50px;
}

.header-text{
    color: rgb(255, 255, 255);
    font-weight: 700;
    font-family:sans-serif;
    text-align: center;
    font-size: 50px;
    
}

.p-text{
    color: rgb(4, 24, 48);
    font-family:Brush Script MT, Brush Script Std, cursive;
    font-size: 35px;
    text-align: center;
}

.jumbo{
    background-color: rgb(231, 158, 63);
}

.card{
    border: 2px solid orangered;
    box-shadow: 2px 4px 4px 3px rgba(77, 37, 22, 0.685);
}

.card-container{
    background-color: rgb(233, 146, 58);
    color: white;
    font-size: 16px;
    height: 150px;
    padding: 10px;
    overflow: hidden;
}